import React, { FC } from 'react';
import {
  ColumnsProps,
  DefaultSortProps,
  ExtraData,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, EmpresaMLF } from '../../../../models/dtos/mlf/empresaMLF';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'empresa.codigo',
    order: OrderType.ASC,
  },
];

const ConfigIpiSearchView: FC<SearchViewProps<EmpresaMLF>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'empresa.codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'empresa.razaoSocial',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Razão Social',
    },
  ];

  const customExtraData: ExtraData = {
    hideRemoveButton: true,
  };

  return (
    <WindowComponent title="Configurações de imposto" wikiId={40}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        addButtonProps={{ hideAddButton: true }}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ConfigImpostoEmpresaEditView', CLASS_NAME, customExtraData)}
      />
    </WindowComponent>
  );
};

export default ConfigIpiSearchView;
